/* eslint-disable @typescript-eslint/no-unused-vars */
import * as z from "zod";
import { X } from "lucide-react";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormMessage,
} from "../ui/form";
import LoginBrowser from "./LoginBrowser";
import { Input } from "../ui/input";
import LoginPassword from "./LoginPassword";

const formSchema = z.object({
  contact: z.string().refine((val) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const phoneRegex = /^\+?[1-9]\d{1,14}$/;
    return emailRegex.test(val) || phoneRegex.test(val);
  }, "  E-mail   "),
});

interface Props {
  isOpen: boolean;
  handleOpen: () => void;
}

const LoginModal = ({ isOpen, handleOpen }: Props) => {
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      contact: "",
    },
  });

  const [inputType, setInputType] = useState<"email" | "tel">("email");
  const [step, setStep] = useState(1);
  const onInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const phoneRegex = /^\+?[1-9]\d{1,14}$/;

    if (value.includes("@")) {
      setInputType("email");
    } else {
      setInputType("tel");
    }

    form.setValue("contact", value);
  };

  const onSubmit = (data: z.infer<typeof formSchema>) => {
    console.log("Form Data:", data);
  };
  const handleClose = () => {
    form.reset();
    setStep(1);
    handleOpen();
  };

  return (
    <>
      <Dialog open={isOpen} onOpenChange={handleClose}>
        <DialogContent className="sm:max-w-[500px] rounded-none top-96 sm:rounded-none p-0">
          <DialogHeader className="px-[50px] py-8">
            <DialogTitle className="font-arial font-normal text-[26px] text-black">
                
            </DialogTitle>
            <button
              onClick={handleOpen}
              className="text-black hover:text-gray-600 focus:outline-none absolute right-4 top-4"
            >
              <X className="w-6 h-6" />
            </button>
          </DialogHeader>
          <div className="px-[50px] ">
            {/* form */}
            <Form {...form}>
              <form
                noValidate
                onSubmit={form.handleSubmit(onSubmit)}
                className="pb-[32px]"
              >
                {step}
              </form>
            </Form>
            {/* or */}
            <div className="pb-[31px] border-b border-beluga flex justify-center">
              <button
                onClick={() => setStep(2)}
                className="font-arial font-normal text-base leading-[25px] text-linkColor group"
              >
                <p className="group-hover:underline hoverEffect">
                    
                </p>
              </button>
            </div>
            {/* footer */}
            <LoginBrowser />
          </div>
        </DialogContent>
      </Dialog>
    </>
  );
};

export default LoginModal;
